ul, li {
  margin: 0;
  padding: 0;
  list-style: none;
}
h1,h2,h3,h4,h5,h6,p{
  margin: 0;
}
a, a:hover {
  display: block;
  text-decoration: none;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}
img {
  max-width: 100%;
  display: block;
}

input, textarea {
  display: inline-block;
}