@import '../../scss/global.scss';

// 
.rec-carousel-wrapper{
  margin-top: 40px;
}
.rec .rec-carousel {
  height: 100%;
}
.rec-carousel .rec-slider {
  min-height: auto;
}
.rec .item__link {
  display: block;
}
.rec .item__link:hover{
  display: block;
}
.rec .item__category_info {
  width: 100%;
  padding-left: 0;
}
.rec .item__wrap {
  height: 200px;
}
.rec .item__descr, .rec .item__more {
  display: none;
}
.rec .rec-dot{
  border: 1px solid $grey-text;
  background-color: $bg-color;
  box-shadow: none;
  width: 10px;
  height: 10px;
  padding: 0;
  &_active {
    background-color: $main-color;
    border: 1px solid $main-color;
  }
  &:hover, &:focus {
    cursor: pointer;
    box-shadow: none;
    border: 1px solid $main-color;
  }
}

@media (max-width: 768px) {
  .rec-carousel-wrapper{
    margin-top: 10px;
  }
  .rec .rec-item-wrapper{
    height: 100%;
  }
  .rec .item__descr, .rec .item__more {
    display: none;
  }
}