@import '../../scss/global.scss';

.article {
  margin-bottom: 40px;
  &__title{
    @include heading( 26px, 700, $main-color);
    margin-bottom: 40px;
  }
  .item__wrap{
    width: 100%;
    margin-bottom: 60px;
  }
  .item__details{
    margin-bottom: 60px;
  }
  p {
    @include heading( 14px, 400, $main-color);
  }
}
.comments {
  
  &__list{
    min-height: 120px;
    h2{
      text-transform: uppercase;
      @include heading( 18px, 700, $main-color);
    }
  }
  &__item{
    display: flex;
    align-items: center;
    padding-left: 20px;
  }
  &__avatar{
    width: 50px;
    height: 50px;
    border-radius: 50%;
    overflow: hidden;
    
  }
  &__details{
    flex: 0 0 80%;
    padding: 10px 0 10px 20px;
  }
  &__username{
    @include heading( 16px, 700, $main-color);
    margin-bottom: 5px;
  }
  &__published{
    @include heading( 10px, 400, $grey-text);
  }
  &__text {
    @include heading( 12px, 400, $main-color);
  }
  &__form {
    margin-top: 40px;
    h2 {
      @include heading(25px, 700, $main-color);
      margin-top: 0;
      text-transform: uppercase;
    }

    span {
      display: inline-block;
      text-transform: uppercase;
      color: $white-text;
      padding: 2px;
      min-width: 100px;
      text-align: center;
      line-height: 1em;
      background: #279cbe;
      margin-bottom: 30px;
      font-size: 11px;
    }
  }

}

@media (max-width: 992px) {
  .article {
    &__title{
      margin-bottom: 20px;
    }
    .item__wrap{
      margin-bottom: 30px;
    }
    .item__details{
      margin-bottom: 30px;
    }
  }
}